html {
    background-color: darkslategray;
    font-family: Tahoma;
}

.box {
    margin: 0;
    padding: 0;
    width: 900px;
    height: 600px;
    top: 50%;
    left: 50%;
    display: flex;
    position: absolute;
    transform: translate(-50%, -50%);
}

.rooms {
    overflow: auto;
    width: 260px;
    height: 580px;
    border-radius: 20px 0px 0px 20px;
    padding: 20px 20px 0px 20px;
    background-color: mediumseagreen
}

.chat {
    width: 600px;
    height: 600px;
    border-radius: 0px 20px 20px 0px;
    background-color: steelblue;
    justify-items: center;
}

.messages {
    width: 560px;
    height: 480px;
    margin: 10px 0px 10px 20px;
    overflow: auto;
}

/*настройки ввода*/
.input-field {
    width: 488px;
    height: 100px;
    padding: 0px 5px 0px 5px;
    margin-left: 2px;
    resize: none;
    outline: none;
    border: none;
    font-size: 1.3em;
    background-color: paleturquoise;
}

.chat input[type = "button"] {
    outline: none;
    border: none;
    padding: 0;
    float: right;
    width: 98px;
    height: 100px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 1.3em;
    border-radius: 0px 0px 20px 0px;
    transition-duration: .25s;
}

.chat input[type = "button"]:hover {
    transition-duration: .25s;
    background-color: springgreen;
}

.chat input[type = "button"]:active {
    transition-duration: .05s;
    background-color: #2bc477;
}

/* настройки сообщений*/
.message {
    width: 400px;
    height: 150px;
    background-color: lightblue;
    margin-bottom: 5px;
}

.message:last-child {
    margin-bottom: 0px;
}

.message-head {
    width: 400px;
    height: 30px;
    background-color: dodgerblue;
    
}

.message-text {
    padding: 5px;
    width: 390px;
    height: 110px;
    background-color: lightblue;
}

.message-username {
    padding: 5px;
    float: left;
}

.message-time {
    padding: 5px;
    float: right;
}

.you-message {
    margin-right: 10px;
    float: right;
}

.another-message {
    margin-left: 10px;
    float: left;
}

.info-message {
    background-color: rgb(230, 126, 147);
    width: 540px;
    height: 30px;
    float: left;
    margin-right: 10px;
    margin-left: 10px;
    margin-bottom: 5px;
    padding-top: 10px;
    text-align: center;
}

.info-message:last-child {
    margin-bottom: 0px;
}


/*Настройски скролл бара*/
::-webkit-scrollbar {
    width: 3px;
    height: 8px;
    background-color: rgba(255, 255, 255, 0);
}

::-webkit-scrollbar-thumb {
    padding-right: 100px;
    background-color: #ffd859;
    border-radius: 9em;
}

/*Настройки отображения комнат*/

.room {
    padding: 5px;
    width: 250px;
    height: 70px;
    background-color: #ba55d3;
    margin-bottom: 5px;
}

.room-list {
    width: 280px;
    height: 560px;
    overflow: auto;
}

.room:last-child {
    margin-bottom: 0px;
}

.room input[type = "button"] {
    width: 70px;
    height: 70px;
    outline: none;
    border: none;
    border-radius: 50%;
    float: right;
    background: linear-gradient(#cddc39, #f44336);
}

.room input[type = "button"]:hover {
    transform: rotateY(720deg);
    transition-duration: 1s;
}

.room-info {
    float: left;
}

.room-name {
    font-size: 1.3em; 
    font-family: Comic Sans MS;
}

.room-participants {
    font-family: monospace;
    padding-top: 25px;
}
