* {
    margin: 0;
    padding: 0;
}

html {
    background-color: darkslategray;
    font-family: cursive;
}

.box-not-found {
    margin: 0;
    padding: 0;
    width: 600px;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    font-size: 4em;
}

.main-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.box-not-found span {
    color: sandybrown;
    text-transform: uppercase;
}

.container-text {
    margin: auto auto;
    display: flex;
    justify-content: center;
    margin: 30px;
}

.spacebar {
    margin: 15px;
}

.box-not-found span {
    color: transparent;
    animation-name: jump;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    text-shadow: 0px 0px 0px sandybrown;
}

.box-not-found span:nth-child(1) {
    animation-delay: 0s;
}

.box-not-found span:nth-child(2) {
    animation-delay: 0.2s;
}

.box-not-found span:nth-child(3) {
    animation-delay: 0.4s;
}

.box-not-found span:nth-child(4) {
    animation-delay: 0.6s;
}

.box-not-found span:nth-child(5) {
    animation-delay: 0.8s;
}

.box-not-found span:nth-child(6) {
    animation-delay: 1.0s;
}

.box-not-found span:nth-child(7) {
    animation-delay: 1.2s;
}

.box-not-found span:nth-child(8) {
    animation-delay: 1.4s;
}

.box-not-found span:nth-child(9) {
    animation-delay: 1.6s;
}

.box-not-found span:nth-child(10) {
    animation-delay: 1.8s;
}

.box-not-found span:nth-child(11) {
    animation-delay: 2s;
}

.box-not-found span:nth-child(12) {
    animation-delay: 2.2s;
}

.box-not-found span:nth-child(13) {
    animation-delay: 2.4s;
}

.box-not-found span:nth-child(14) {
    animation-delay: 2.6s;
}




@keyframes jump {
    0%, 100% {
        text-shadow: 0px 0px 0px sandybrown;
    }
    35%, 65% {
        text-shadow: 0px -50px 0px sandybrown;
        
    }
}