html {
    background-color: darkslategray;
    font-family: Tahoma;
}

.box {
    margin: 0;
    padding: 0;
    width: 600px;
    height: 400px;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
}

.box h1 {
    margin: 0;
    padding: 0;
    text-shadow: -20px -15px 3px #59afcc;
    transition: 1s;
    margin-left: 30%;
}

.panel-login {
    width: 400px;
    height: 220px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: inherit;
    background-color: black;
    text-align: center;
    box-shadow: 0 0 20px 20px #59afcc;
    transition: 1s;
}

.panel-login:hover {
    transition: 5s;
    box-shadow: 0 0 5px 5px #59afcc;
}

.panel-login input[type="text"], .panel-login input[type="password"] {
    width: 180px;
    height: 20px;
    border: 0;
    background: 0;
    outline: none;
    margin: 25px auto;
    display: block;
    text-align: center;
    box-shadow: 0px 0px 5px 3px #59AFCC;
    color: white;
    transition: .3s;
}

.panel-login input[type="text"]:focus, .panel-login input[type="password"]:focus {
    transition: .3s;
    height: 30px;
    width: 250px;
    box-shadow: 0px 0px 10px 10px #59AFCC;
    border-radius: 25px;
}

.buttons {
    width: 180px;
    margin: 25px auto;
}

.buttons input[type="submit"] {
    background: 0;
    border: 0;
    outline: none;
    width: 80px;
    height: 25px;
    box-shadow: 0 0 5px 5px #59afcc;
    transition: 1s;
    color: white;
}

.buttons input[type="submit"]:active {
    box-shadow: 0 0 10px 10px #59afcc;
    transition: .15s;
}

#btn-join {
    float: left;
}

#btn-create {
    float: right;
}